
import Vue from 'vue'
import ToolbarSticky from './ToolbarSticky.vue'

export default Vue.extend({
  name: 'MultiSelectBar',
  components: { ToolbarSticky },
  data: () => {
    return {}
  },
  props: {
    value: {
      type: Array,
      required: true,
    },
    /**
     * The kind of items this toolbar acts on.
     * @values books, series, collections, readlists
     */
    kind: {
      type: String,
      required: true,
    },
    oneshots: {
      type: Boolean,
      default: false,
    },
    showSelectAll: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isAdmin (): boolean {
      return this.$store.getters.meAdmin
    },
  },
  methods: {
    unselectAll () {
      this.$emit('unselect-all')
    },
    selectAll () {
      this.$emit('select-all')
    },
    markRead () {
      this.$emit('mark-read')
    },
    markUnread () {
      this.$emit('mark-unread')
    },
    addToCollection () {
      this.$emit('add-to-collection')
    },
    addToReadList () {
      this.$emit('add-to-readlist')
    },
    edit () {
      this.$emit('edit')
    },
    bulkEdit () {
      this.$emit('bulk-edit')
    },
    doDelete () {
      this.$emit('delete')
    },
  },
})
